<template>
  <!-- v-theme:column="'wide'" main.js里设定的自定义指令-->
  <div id="show-blogs">
    <div class="jumbotron">
      <h1>ᠤᠲᠠᠴᠢ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ ——</h1>
      <p>ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠪᠤᠶᠤ ᠲᠤᠰᠠᠯᠠᠬᠤ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠳ᠋ᠦ ᠬᠠᠨᠳᠤᠭᠤᠯᠤᠭᠰᠠᠨ ᠴᠤᠷᠢ ᠢᠨ ᠭᠠᠭᠴᠠ ᠲᠤᠰᠬᠠᠢᠵᠢᠭᠰᠠᠨ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ</p>
    
      <!-- <p><a class="btn btn-primary btn-lg" href="#" role="button">Learn more</a></p> -->
    </div>
    <div class="content">
      <h3>ᠤᠷᠴᠢᠮ ᠤᠨ ᠬᠢᠴᠢᠶᠡᠯ ᠲᠤᠬᠢᠷᠠᠭᠤᠯᠭ᠎ᠠ</h3>
      <input type="text" v-model="search" placeholder="ᠬᠠᠢᠬᠤ">
      <div v-for="blog in filteredBlogs" class="single-blog">
      	<router-link v-bind:to="'/blog/' + blog.id">
          <h4 v-rainbow>{{blog.title | to-uppercase}}</h4>
        </router-link>
      	<article>{{blog.content | snippet}}</article>
      </div>
      <div>
        <h4>ᠮᠤᠩᠭᠤᠯ ᠡᠮᠨᠡᠯᠭᠡ ᠢᠨ ᠠᠵᠢᠯ ᠡᠷᠬᠢᠯᠡᠬᠦ ᠡᠮᠴᠢ ᠵᠢᠴᠢ ᠲᠤᠰᠠᠯᠠᠬᠤ ᠡᠮᠴᠢ ᠢᠨ 
    ᠰᠢᠯᠭᠠᠯᠲᠠ ᠳ᠋ᠦ ᠬᠠᠨᠳᠤᠭᠤᠯᠤᠭᠰᠠᠨ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ ᠡᠷᠬᠢᠯᠡᠬᠦ ᠮᠡᠳᠡᠭᠳᠡᠯ </h4>
        <article>    
          ᠤᠲᠠᠴᠢ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ ᠪᠤᠯ ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠪᠤᠶᠤ ᠲᠤᠰᠠᠯᠠᠬᠤ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠳ᠋ᠦ ᠬᠠᠨᠳᠤᠭᠤᠯᠤᠭᠰᠠᠨ ᠴᠤᠷᠢ ᠢᠨ ᠭᠠᠭᠴᠠ ᠲᠤᠰᠬᠠᠢᠵᠢᠭᠰᠠᠨ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ ᠪᠠᠢᠵᠤ  ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠢᠨ ᠤᠳᠠᠭᠠᠨ ᠵᠢᠯ ᠤᠨ ᠰᠤᠷᠤᠯᠴᠠᠬᠤ ᠠᠴᠠ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠭᠠᠵᠠᠷ ᠦᠭᠡᠢ  ᠲᠠᠪᠲᠠᠬᠣ ᠠᠴᠠ ᠯᠠᠪᠯᠠᠬᠤ ᠮᠠᠲᠧ᠋ᠷᠢᠶᠠᠯ ᠦᠭᠡᠢ ᠪᠠᠢᠳᠠᠯ ᠢ ᠡᠴᠦᠰᠯᠡᠭᠦᠯᠵᠦ  ᠦᠷᠭᠡᠨ ᠤᠯᠠᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠰᠢᠯᠭᠠᠭᠤᠯᠤᠭᠰᠠᠳ ᠲᠦ ᠲᠦᠭᠦᠮ ᠲᠦᠳᠡ ᠦᠢᠯᠡᠴᠢᠯᠡᠭᠡ ᠬᠠᠩᠭᠠᠵᠤ  ᠲᠡᠳᠡᠨᠣᠰ ᠤᠨ ᠡᠷᠲᠡ ᠨᠢᠭᠡ ᠡᠳᠦᠷ ᠠᠵᠢᠯ ᠤᠨ ᠪᠠᠢᠷᠢ ᠪᠠᠨ ᠤᠯᠵᠤ  ᠬᠦᠮᠦᠨ ᠲᠦᠷᠦᠯᠬᠢᠲᠡᠨ ᠤ ᠡᠷᠡᠭᠦᠯ ᠡᠩᠬᠡ ᠳ᠋ᠦ ᠨᠡᠮᠡᠷᠢ ᠬᠠᠨᠳᠢᠪ ᠡᠷᠭᠦᠬᠦ ᠳ᠋ᠦ ᠨᠢ ᠰᠢᠯᠤᠭᠤᠨ ᠲᠠᠷᠳᠠᠡᠭ ᠵᠠᠮ ᠲᠡᠪᠢᠰᠴᠦ ᠦᠭᠭᠦᠭᠰᠡᠨ ᠪᠠᠢᠨ᠎ᠠ  ᠮᠠᠨ ᠤ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ ᠤᠨ ᠤᠷᠢᠨ ᠵᠠᠯᠠᠭᠰᠠᠨ ᠪᠠᠬᠰᠢ ᠢᠨ ᠡᠩᠨᠡᠭᠡ ᠪᠤᠯ ᠮᠤᠩᠭᠤᠯ ᠡᠮ ᠡᠮᠨᠡᠯᠭᠡ ᠢᠨ ᠲᠠᠯ᠎ᠠ ᠪᠡᠷ ᠴᠤᠯᠭᠠᠷᠠᠭᠰᠠᠨ  ᠡᠯ᠎ᠡ ᠭᠠᠵᠠᠷ ᠤᠨ ᠡᠮᠨᠡᠯᠭᠡ ᠶᠢᠨ ᠬᠤᠷᠢᠶ᠎ᠠ ᠪᠠ ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠡᠮᠨᠡᠯᠭᠡ ᠤᠨ ᠤᠬᠠᠭᠠᠨ ᠤ ᠶᠡᠬᠡ ᠰᠤᠷᠭᠠᠭᠤᠯᠢ  ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠦᠨᠳᠦᠰᠦᠲᠡᠨ ᠤ ᠶᠡᠬᠡ ᠰᠤᠷᠭᠠᠭᠤᠯᠢ ᠶᠢᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮ ᠡᠮᠨᠡᠯᠭᠡ ᠶᠢᠨ ᠰᠠᠯᠪᠤᠷᠢ ᠢᠨ ᠨᠡᠷ᠎ᠡ ᠬᠦᠨᠳᠦᠳᠦ ᠡᠮᠴᠢ  ᠪᠠᠬᠰᠢ ᠨᠠᠷ ᠢᠶᠡᠷ ᠪᠦᠷᠢᠯᠳᠦᠵᠠᠢ  ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠠᠵᠢᠯ ᠡᠷᠬᠢᠯᠡᠬᠦ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠪᠤᠶᠤ ᠲᠤᠰᠠᠯᠠᠬᠤ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠳ᠋ᠦ ᠬᠠᠨᠳᠤᠭᠤᠯᠤᠭᠰᠠᠨ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ ᠢᠨ ᠴᠠᠭ ᠬᠤᠭᠤᠴᠠᠭ᠎ᠠ ᠪᠡᠷ ᠬᠠᠷᠢᠴᠠᠩᠭᠤᠢ ᠣᠷᠲᠣ ᠪᠤᠯᠬᠤᠷ ᠦᠷᠭᠡᠨ ᠤᠯᠠᠨ ᠰᠢᠯᠭᠠᠭᠤᠯᠤᠭᠰᠠᠳ ᠡᠷᠲᠡ ᠰᠢᠭ ᠰᠤᠷᠤᠯᠴᠠᠬᠤ ᠴᠠᠭ ᠢᠶᠠᠨ ᠰᠠᠢᠲᠤᠷ ᠲᠤᠬᠢᠷᠠᠭᠤᠯᠵᠤ  ᠲᠤᠰ ᠵᠢᠯ ᠳ᠋ᠦ ᠪᠠᠨ ᠦᠨᠡᠮᠯᠡᠯ ᠢᠶᠠᠨ ᠭᠠᠷᠳᠠᠬᠤ ᠶᠢ ᠴᠢᠨᠢ ᠤᠷᠢᠳᠴᠢᠯᠠᠨ ᠵᠦᠩᠨᠡᠶ᠎ᠡ   ᠬᠢᠴᠢᠶᠡᠯ ᠤᠨ ᠲᠤᠬᠢᠷᠠᠭᠤᠯᠭ᠎ᠠ ᠵᠢᠴᠢ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ ᠢᠨ ᠰᠦᠢᠳᠭᠡᠯ ᠤᠨ ᠲᠤᠬᠠᠢ ᠤᠢᠯᠠᠭᠠᠬᠤ ᠭᠡᠪᠡᠯ ᠰᠢᠭᠤᠳ ᠬᠠᠷᠢᠯᠴᠠᠬᠤ ᠤᠲᠠᠰᠤ ᠪᠤᠶᠤ ᠸᠸᠴᠠᠲ ᠢᠶᠠᠷ ᠬᠤᠯᠪᠤᠭ᠎ᠠ ᠪᠠᠷᠢᠬᠤ ᠪᠣᠯᠪᠠᠤ 
        </article>
        <p><a href="tel:15248184142">ᠬᠠᠷᠢᠯᠴᠠᠬᠤ ᠤᠲᠠᠰᠤ᠄ 152-4818-4142</a></p>
        <img src="./../assets/anar.png">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data(){
	return{
		blogs:[],
		search:""
	}  	
  },
  created(){
  	// http://jsonplaceholder.typicode.com/posts
    //本地json文件 './../static/posts.json'
  	this.$http.get('https://vue-blog-jun.firebaseio.com/posts.json')
  		.then(function(data){
  			console.log(data.json());
  			//this.blogs = data.body.slice(0,10);
        return data.json(); //json解析
  		})
      .then(function(data){
        var blogsArray = [];
        console.log(data);
        for(let key in data){
          //console.log(data[key]);
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      })
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) => {
  			return blog.title.match(this.search);
  		})
  	}
  },
  filters:{
  	// "to-uppercase":function(value){
  	// 	return value.toUpperCase();
  	// }
  	//此方法是上面的"to-uppercase":function(value)一样，写法不一样而已
  	toUppercase(value){
  		return value.toUpperCase();
  	}
  },
  //全局的指令改为局部的指令
  directives:{
  	"rainbow":{
  		bind(el,binding,vode){
  			el.style.color = "#" + Math.random().toString(16).slice(2,8);
  		}
  	}
  }
}
</script>

<style>
	#show-blogs{
		max-height: 100%;
		margin: auto 0;
	}
	.single-blog{
		background:#eee;
		margin: 0 20px;
		padding: 20px;
		box-sizing: border-box;
		border-radius: 8px;
    height: 100%;
	}
	h4{
		margin: 0 0 10px 0;
		color: #093f70;
    line-height: 1.5;
    padding: 30px 0;
	}
  #show-blogs a{
    text-decoration: none;
  }
  input[type="text"]{
    display: table-cell;
    margin-right: 20px;
    padding: 8px;
    height: 100%;
    min-width: 50px;
    box-sizing: border-box; 
  }
  .jumbotron{
    margin:0;
  }
  .jumbotron p{
    font-family: urga;
    font-size: 18px;
    padding: 80px 20px;
  }
  .jumbotron h1{
    font-family: urga;
    padding-top: 30px;
  }
  img{
    width: 150px;
    margin-right: 20px;
  }
</style>



