<template>
	<div class="content">
		<h3>ᠮᠠᠨ ᠤ ᠲᠤᠬᠠᠢ</h3>
		<article>
			ᠤᠲᠠᠴᠢ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ ᠪᠤᠯ ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠪᠤᠶᠤ ᠲᠤᠰᠠᠯᠠᠬᠤ ᠡᠮᠴᠢ ᠢᠨ ᠬᠢᠷᠢ ᠬᠡᠮ ᠤᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠳ᠋ᠦ ᠬᠠᠨᠳᠤᠭᠤᠯᠤᠭᠰᠠᠨ ᠴᠤᠷᠢ ᠢᠨ ᠭᠠᠭᠴᠠ ᠲᠤᠰᠬᠠᠢᠵᠢᠭᠰᠠᠨ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠠᠩᠭᠢ ᠪᠠᠢᠵᠤ  ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠢᠨ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠢᠨ ᠤᠳᠠᠭᠠᠨ ᠵᠢᠯ ᠤᠨ ᠰᠤᠷᠤᠯᠴᠠᠬᠤ ᠠᠴᠠ ᠪᠤᠯᠪᠠᠰᠤᠷᠠᠯ ᠤᠨ ᠭᠠᠵᠠᠷ ᠦᠭᠡᠢ  ᠲᠠᠪᠲᠠᠬᠣ ᠠᠴᠠ ᠯᠠᠪᠯᠠᠬᠤ ᠮᠠᠲᠧ᠋ᠷᠢᠶᠠᠯ ᠦᠭᠡᠢ ᠪᠠᠢᠳᠠᠯ ᠢ ᠡᠴᠦᠰᠯᠡᠭᠦᠯᠵᠦ  ᠦᠷᠭᠡᠨ ᠤᠯᠠᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮᠴᠢ ᠰᠢᠯᠭᠠᠭᠤᠯᠤᠭᠰᠠᠳ ᠲᠦ ᠲᠦᠭᠦᠮ ᠲᠦᠳᠡ ᠦᠢᠯᠡᠴᠢᠯᠡᠭᠡ ᠬᠠᠩᠭᠠᠵᠤ  ᠲᠡᠳᠡᠨᠣᠰ ᠤᠨ ᠡᠷᠲᠡ ᠨᠢᠭᠡ ᠡᠳᠦᠷ ᠠᠵᠢᠯ ᠤᠨ ᠪᠠᠢᠷᠢ ᠪᠠᠨ ᠤᠯᠵᠤ  ᠬᠦᠮᠦᠨ ᠲᠦᠷᠦᠯᠬᠢᠲᠡᠨ ᠤ ᠡᠷᠡᠭᠦᠯ ᠡᠩᠬᠡ ᠳ᠋ᠦ ᠨᠡᠮᠡᠷᠢ ᠬᠠᠨᠳᠢᠪ ᠡᠷᠭᠦᠬᠦ ᠳ᠋ᠦ ᠨᠢ ᠰᠢᠯᠤᠭᠤᠨ ᠲᠠᠷᠳᠠᠡᠭ ᠵᠠᠮ ᠲᠡᠪᠢᠰᠴᠦ ᠦᠭᠭᠦᠭᠰᠡᠨ ᠪᠠᠢᠨ᠎ᠠ  ᠮᠠᠨ ᠤ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ ᠤᠨ ᠤᠷᠢᠨ ᠵᠠᠯᠠᠭᠰᠠᠨ ᠪᠠᠬᠰᠢ ᠢᠨ ᠡᠩᠨᠡᠭᠡ ᠪᠤᠯ ᠮᠤᠩᠭᠤᠯ ᠡᠮ ᠡᠮᠨᠡᠯᠭᠡ ᠢᠨ ᠲᠠᠯ᠎ᠠ ᠪᠡᠷ ᠴᠤᠯᠭᠠᠷᠠᠭᠰᠠᠨ  ᠡᠯ᠎ᠡ ᠭᠠᠵᠠᠷ ᠤᠨ ᠡᠮᠨᠡᠯᠭᠡ ᠶᠢᠨ ᠬᠤᠷᠢᠶ᠎ᠠ ᠪᠠ ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠡᠮᠨᠡᠯᠭᠡ ᠤᠨ ᠤᠬᠠᠭᠠᠨ ᠤ ᠶᠡᠬᠡ ᠰᠤᠷᠭᠠᠭᠤᠯᠢ  ᠦᠪᠦᠷ ᠮᠤᠩᠭᠤᠯ ᠤᠨ ᠦᠨᠳᠦᠰᠦᠲᠡᠨ ᠤ ᠶᠡᠬᠡ ᠰᠤᠷᠭᠠᠭᠤᠯᠢ ᠶᠢᠨ ᠮᠤᠩᠭᠤᠯ ᠡᠮ ᠡᠮᠨᠡᠯᠭᠡ ᠶᠢᠨ ᠰᠠᠯᠪᠤᠷᠢ ᠢᠨ ᠨᠡᠷ᠎ᠡ ᠬᠦᠨᠳᠦᠳᠦ ᠡᠮᠴᠢ  ᠪᠠᠬᠰᠢ ᠨᠠᠷ ᠢᠶᠡᠷ ᠪᠦᠷᠢᠯᠳᠦᠵᠠᠢ  
		</article>
